.footer {
  padding-top: 60px;
  padding-bottom: 4px;
  @media screen and (max-width: 767px) {
    padding-top: 30px;
  }
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    padding-top: 20px;
  }
  scroll-behavior: smooth;
}

.footer__border {
  border-bottom: 1px solid #e1e1e1;
  @media screen and (max-width: 767px) {
    margin-bottom: 16px;
    margin: 0 auto;
    width: 100vw;
  }
}

.footer__border {
  display: flex;
  justify-content: space-between;
  padding-left: 16px;
  padding-right: 16px;
  @media screen and (max-width: 767px) {
    display: flex;
    flex-wrap: wrap;
  }
}

.footer__p {
  margin-bottom: 16px;
  font-family: 'Titan One', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.16;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #ffa5ba;
  line-height: 1.17;
  text-transform: uppercase;
  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
    letter-spacing: 0.04em;
  }
  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 1.13;
  }
  @media screen and (min-width: 768px) and (max-width: 1200px) {
    width: 340px;
  }
  @media screen and (max-width: 767px) {
    width: 232px;
  }
}

.footer__title--red {
  font-weight: 400;
  font-family: Titan One, sans-serif;
  color: #d41443;
  font-size: 34px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.15;
    letter-spacing: 0.06em;
  }
  @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 1.14;
    letter-spacing: 0.06em;
    display: flex;
  }
}

.footer__list {
  display: flex;
  gap: 40px;
  justify-content: end;
  margin-bottom: 40px;
  padding-right: 20px;
  @media screen and (min-width: 768px) {
    gap: 30px;
  }
  @media screen and (min-width: 768px) {
    gap: 30px;
  }
}

.footer__text {
  margin-bottom: 40px;
  text-align: end;
  font-family: DM sans, sans-serif;
  font-weight: 700;
  font-size: 12px;
  letter-spacing: 1.86;
  color: #000000;
  @media screen and (min-width: 768px) {
    font-size: 14px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
    letter-spacing: 0.02em;
  }
}

.footer__title {
  font-family: 'Titan One', sans-serif;
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    font-size: 48px;
    line-height: 55px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    margin-bottom: 40px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 1.13;
    letter-spacing: 0.06em;
  }
  text-transform: uppercase;
  color: #d41443;
  @media screen and (max-width: 767px) {
    font-size: 34px;
    line-height: 1.14;
    letter-spacing: 0.04em;
  }
  @media screen and (min-width: 768px) and (max-width: 1200px) {
    width: 340px;
  }
  @media screen and (max-width: 767px) {
    width: 232px;
  }
}

.footer__phone {
  transition: all 250ms ease-in;
  color: #d41443;
  &:hover,
  &:focus {
    color: #ffa5ba;
  }
}

.footer__text--rights {
  text-align: center;
  font-family: DM sans, sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.52;
  color: #907e82;
  @media screen and (max-width: 767px) {
    margin: 0;
  }
}

.footer__text--color {
  transition: color 250ms ease-in;
  color: #d41443;
}

.footer__icon {
  fill: #ffffff;
}

.footer__link {
  border-radius: 50%;
  background-color: #fea5bb;
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 250ms ease-in;
  &:hover,
  &:focus {
    background-color: #ecebeb;
  }
}

.footer__link:hover .footer__icon,
.footer__link:focus .footer__icon {
  fill: $mobileColor;
}

.footer__box--right {
  @media screen and (max-width: 767px) {
    margin-top: 16px;
  }
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    margin-top: 8px;
    display: flex;
    justify-content: center;
    text-align: center;
  }
  @media screen and (min-width: 1200px) {
    display: flex;
    justify-content: center;
    margin-top: 7px;
  }
}

.footer__box--desk {
  @media screen and (min-width: 1200px) {
    display: flex;
    justify-content: center;
  }
}

.footer__list-position {
  align-items: baseline;
  align-self: flex-end;
  @media screen and (max-width: 767px) {
    margin-bottom: 40px;
  }
}

.footer__position {
  @media screen and (max-width: 767px) {
    margin-bottom: 40px;
  }
}

.footer__border {
  margin: 0 auto;
  width: 100%;
  @media screen and (min-width: 480px) {
    width: 480px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    padding-bottom: 40px;
  }
  @media screen and (min-width: 1200px) {
    width: 1200px;
    padding-bottom: 60px;
  }
}

.button__contacts--red:hover,
.button__contacts--red:focus {
  background-color: #ffffff;
  color: #d41442;
}

.button__contacts--white:hover,
.button__contacts--white:focus {
  background-color: #d41342;
  color: #ffffff;
}

.button__contacts--red,
.button__contacts--white {
  transition: all 250ms ease-in;
}
